.footer {
  padding: 10px;
  width: 100%;
  background-color: #e3e3e3;
  display: flex;

  div {
    margin: 0 5px;
  }

  span {
    font-size: 14px;
  }

  @media (max-width: 720px) {
    flex-direction: column;
    overflow: auto;
    height: 70px;
    div{
      margin: 5px;
    }

  }
}